import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Checkbox, Typography } from "@rp/react/web/mui/core"

type filterInputedTypes = {
  isEntity: boolean
}

export const FilterInputStyled = styled("div", {
  name: "FilterInputStyled",
  shouldForwardProp: props => props !== "isEntity" && props !== "showWatchlistIcon",
})<filterInputedTypes>(({ theme, isEntity }) => {
  const { isMobile } = useDeviceInfo()
  return {
    // Common styles
    display: "flex",
    alignItems: "center",
    margin: isMobile ? "8px 0 8px 0" : "2px 0 2px 0",
    minHeight: "28px",
    "& .MuiCheckbox-root": {
      marginRight: "12px",
      marginLeft: "3px",
    },
    "& .MuiCheckbox-root:not(.Mui-checked)": {
      opacity: isMobile ? 1 : 0,
    },
    // Desktop only
    "@media (hover: hover)": {
      "& .MuiCheckbox-root": {
        marginRight: "2px",
      },
      "& button": {
        display: "none",
      },

      "&:hover": {
        backgroundColor: theme.palette.grey[800],
        cursor: isEntity ? "pointer" : "default",
        "& .MuiCheckbox-root": {
          opacity: 1,
          color: theme.palette.text.secondary,
        },
        "& .MuiCheckbox-root:hover": {
          color: theme.palette.text.primary,
        },
        "& button": {
          display: "flex",
          color: theme.palette.text.secondary,
        },
        "& button:hover": {
          display: "flex",
          color: theme.palette.text.primary,
        },
      },
    },
    // Mobile only
    "@media (hover: none)": {
      ".watchlistMenu": { display: "flex" },
    },
  }
})

type CheckboxType = {
  inWatchlist: boolean
  disableCheckbox: boolean
}
export const CheckboxStyled = styled(Checkbox, {
  name: "CheckboxStyled",
  shouldForwardProp: props => props !== "inWatchlist" && props !== "disableCheckbox",
})<CheckboxType>(({ theme, inWatchlist, disableCheckbox }) => {
  const { isMobile } = useDeviceInfo()
  return {
    height: "28px",
    width: "28px",
    marginLeft: "4px",
    marginRight: "2px",

    "& svg": {
      height: isMobile ? 24 : 16,
      width: isMobile ? 24 : 16,
    },

    "&.MuiCheckbox-root": {
      color: theme.palette.grey[100],
      "&:hover": {
        color: disableCheckbox ? "transparent !important" : theme.palette.primary.main,
        cursor: disableCheckbox ? "default !important" : "pointer",
      },
    },
    "&.Mui-checked": {
      opacity: 1,
      color: theme.palette.primary.main,
      "& .MuiSvgIcon-root": {
        color: inWatchlist ? theme.palette.grey[400] : theme.palette.primary.main,
      },
    },
    "&.Mui-disabled:not(.Mui-checked)": {
      color: disableCheckbox ? "transparent !important" : "inherit",
      cursor: disableCheckbox ? "default !important" : "pointer",
    },
  }
})

export const CounterStyled = styled(Typography, { name: "CounterStyled" })(({ theme }) => ({
  color: theme.palette.grey[100],
  cursor: "default",
  flexShrink: 0,
  textAlign: "end",
  width: 44,
}))

export const FilterInputLabelStyled = styled("div", { name: "FilterInputLabel" })({
  display: "flex",
  flexGrow: 1,
  minWidth: 0,
  cursor: "pointer",
  alignItems: "center",
})
